import React, {Component} from 'react';
import { Modal, Button, Form } from 'antd';

class Project extends Component {

    constructor() {
        super();
        this.state = {
            visible: false,
            user: {
                _id:""
            },
            data: {}
        };
    }

        showModal = () => {
            this.setState({
                visible: true,
            });
        };

        handleOk = (e) => {
            console.log(e);
            this.setState({
                visible: false,

            });
        };

        handleCancel = (e) => {
            console.log(e);
            this.setState({
                visible: false,
            });
        };



    render(){
        return(
            <div>

                <Button type="primary" onClick={this.showModal}>
                Agregar Prioridad
                </Button>
                <Modal
                title="Basic Modal"
                visible={this.state.visible}
                onOk={this.handleOk}
                onCancel={this.handleCancel}
                >
                    <Form action="">
                        <label htmlFor="">
                            Nombre:
                            <input name="name" type="text"/>
                        </label>
                        <label htmlFor="">
                            Encargado:
                            <input name="_creator" type="text"/>
                        </label>
                        <label htmlFor="">
                            Descripción:
                            <textarea name="description" type="text"> </textarea>
                        </label>
                    </Form>
                </Modal>
                </div>
                );
            }

}

export default Project